.container {
  width: 100%;
  max-width: 1440px;
  background-color: hsl(0deg, 100%, 74%);
  background-image: url(../images/bg-intro-desktop.png);
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-family: "Poppins", sans-serif;
  padding-block: 4rem;
  padding-inline: 4rem;
}
.container .introContent {
  color: white;
  max-width: 35rem;
  padding-inline: 1rem;
}
.container .introContent .title {
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  font-size: 3rem;
  padding-block: 1rem;
}
.container .introContent .subTitle {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  padding-block: 1rem;
}
.container .introForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  padding-block: 3rem;
  gap: 1.5rem;
  max-width: 40rem;
}
.container .introForm .formTitle {
  color: white;
  background-color: hsl(248deg, 32%, 49%);
  border-radius: 0.5rem;
  padding: 1rem;
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  box-shadow: 0 0.5rem 0.5rem hsl(249deg, 10%, 26%);
}
.container .introForm .formTitle span {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}
.container .introForm #signupForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  gap: 0.5rem;
  background-color: white;
  padding: 4rem 2rem;
  border-radius: 0.5rem;
  width: 100%;
  box-shadow: 0 0.5rem 0.5rem hsl(249deg, 10%, 26%);
}
.container .introForm #signupForm input {
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid hsl(246deg, 25%, 77%);
}
.container .introForm #signupForm input[type=text] {
  padding-inline-start: 1.5rem;
  background: url(../images/icon-error.svg) no-repeat 95%;
}
.container .introForm #signupForm input[type=text]:hover {
  border-color: hsl(154deg, 59%, 51%);
}
.container .introForm #signupForm input[type=text]:focus {
  border: none;
  outline: 1px solid black;
}
.container .introForm #signupForm input[type=submit] {
  background-color: hsl(154deg, 59%, 51%);
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.container .introForm #signupForm input[type=submit]:hover {
  background-color: hsl(154deg, 59%, 71%);
  cursor: pointer;
  border-bottom: 0.3rem solid hsl(154deg, 59%, 51%);
}
.container .introForm #signupForm .errorMessage {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 0.7rem;
  text-align: right;
  color: hsl(0deg, 100%, 74%);
  font-style: italic;
  padding-bottom: 1rem;
}
.container .introForm #signupForm .formFooter {
  font-size: 0.7rem;
  text-align: center;
  padding-inline: 1.5rem;
  color: hsl(246deg, 25%, 77%);
}
.container .introForm #signupForm .formFooter .terms {
  font-size: 0.7rem;
  color: hsl(0deg, 100%, 74%);
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

@media only screen and (max-width: 660px) {
  .container {
    height: 100%;
    flex-direction: column;
    overflow: auto;
    align-items: center;
    justify-content: flex-start;
  }
}
@media only screen and (max-width: 500px) {
  .container {
    height: 100%;
    flex-direction: column;
    overflow: auto;
    align-items: center;
    justify-content: flex-start;
  }
  .container .introContent {
    max-width: 100%;
    padding-inline: 0;
    text-align: center;
  }
  .container .introContent .title {
    font-size: 2rem;
    padding-block: 0;
  }
  .container .introContent .subTitle {
    padding-block-start: 1rem;
  }
  .container .introForm {
    padding-block: 1rem;
  }
  .container .introForm #signupForm {
    gap: 0;
    padding: 2rem;
  }
  .container .introForm #signupForm .formFooter {
    padding-block: 1rem;
  }
}
@media only screen and (max-width: 400px) {
  .container {
    padding-inline: 1rem;
  }
  .container .introContent {
    width: 100%;
    max-width: 375px;
  }
  .container .introContent .title {
    width: 100%;
    font-size: 1.5rem;
  }
  .container .introForm .formTitle {
    padding: 1rem 4rem;
  }
  .container .introForm #signupForm {
    padding: 1rem;
  }
}