//Layout
$maxWidth_Mobile: 375px;
$maxWidth_Desktop: 1440px;

//Colors
$color_primary_Red: hsl(0, 100%, 74%);
$color_primary_Green: hsl(154, 59%, 51%);
$color_primary_lighterGreen: hsl(154, 59%, 71%);
$color_accent_Blue: hsl(248, 32%, 49%);
$color_neutral_darkBlue: hsl(249, 10%, 26%);
$color_neutral_GrayishBlue: hsl(246, 25%, 77%);

//Fonts
$mainFont: 'Poppins', sans-serif;

@mixin regular_mainFont {
    font-family: $mainFont;
    font-weight: 400;
}

@mixin medium_mainFont {
    font-family: $mainFont;
    font-weight: 500;
}

@mixin semiBold_mainFont {
    font-family: $mainFont;
    font-weight: 600;
}

@mixin bold_mainFont {
    font-family: $mainFont;
    font-weight: 700;
}

.container {
    width: 100%;
    max-width: $maxWidth_Desktop;
    background-color: $color_primary_Red;
    background-image: url(../images/bg-intro-desktop.png);
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-family: $mainFont;
    padding-block: 4rem;
    padding-inline: 4rem;

    .introContent {
        color: white;
        max-width: 35rem;
        padding-inline: 1rem;

        .title {
            @include bold_mainFont();
            font-size: 3rem;
            padding-block: 1rem;
        }

        .subTitle {
            @include regular_mainFont();
            padding-block: 1rem;
        }
    }

    .introForm {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: stretch;
        padding-block: 3rem;
        gap: 1.5rem;
        max-width: 40rem;

        .formTitle {
            color: white;
            background-color: $color_accent_Blue;
            border-radius: .5rem;
            padding: 1rem;
            text-align: center;
            @include regular_mainFont();
            box-shadow: 0 .5rem .5rem $color_neutral_darkBlue;

            span {
                @include semiBold_mainFont();
            }
        }

        #signupForm {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: stretch;
            gap: .5rem;
            background-color: white;
            padding: 4rem 2rem;
            border-radius: .5rem;
            width: 100%;
            box-shadow: 0 .5rem .5rem $color_neutral_darkBlue;

            input {
                @include semiBold_mainFont();
                padding: 1rem;
                border-radius: .5rem;
                border: 1px solid $color_neutral_GrayishBlue;

                &[type='text'] {
                    padding-inline-start: 1.5rem;
                    background: url(../images/icon-error.svg) no-repeat 95%;

                    &:hover {
                        border-color: $color_primary_Green;
                    }

                    &:focus {
                        border: none;
                        outline: 1px solid black;
                    }
                }

                &[type='submit'] {
                    background-color: $color_primary_Green;
                    color: white;
                    text-transform: uppercase;
                    letter-spacing: 2px;

                    &:hover {
                        background-color: $color_primary_lighterGreen;
                        cursor: pointer;
                        border-bottom: .3rem solid $color_primary_Green;
                    }

                }
            }

            .errorMessage {
                @include regular_mainFont();
                font-size: .7rem;
                text-align: right;
                color: $color_primary_Red;
                font-style: italic;
                padding-bottom: 1rem;
            }

            .formFooter {
                font-size: .7rem;
                text-align: center;
                padding-inline: 1.5rem;
                color: $color_neutral_GrayishBlue;

                .terms {
                    font-size: .7rem;
                    color: $color_primary_Red;
                    text-decoration: none;
                    @include semiBold_mainFont();
                }
            }

        }

    }
}

@media only screen and (max-width: 660px) {
    .container {
        height: 100%;
        flex-direction: column;
        overflow: auto;
        align-items: center;
        justify-content: flex-start;
    }
}

@media only screen and (max-width: 500px) {
    .container {
        height: 100%;
        flex-direction: column;
        overflow: auto;
        align-items: center;
        justify-content: flex-start;

        .introContent {
            max-width: 100%;
            padding-inline: 0;
            text-align: center;

            .title {
                font-size: 2rem;
                padding-block: 0;
            }

            .subTitle {
                padding-block-start: 1rem;
            }

        }

        .introForm {
            padding-block: 1rem;

            #signupForm {
                gap: 0;
                padding: 2rem;

                .formFooter {
                    padding-block: 1rem;
                }
            }
        }

    }
}

@media only screen and (max-width: 400px) {
    .container {
        padding-inline: 1rem;

        .introContent {
            width: 100%;
            max-width: $maxWidth_Mobile;

            .title {
                width: 100%;
                font-size: 1.5rem;
            }
        }

        .introForm {
            .formTitle {
                padding: 1rem 4rem;
            }

            #signupForm {
                padding: 1rem;
            }
        }
    }
}